.bg-card-container {
    padding: 20px 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.bg-card {
    padding: 35px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.bg-card:hover{
    transition: all 0.2s ease-out;
    box-shadow: 0px 4px 8px rgba(38, 38, 38, 0.7);
    top: -4px;
    border: 1px solid #cccccc;
    border-radius: 5px;
}

.yellow:hover{
    background: yellow;
}

.pink:hover{
    background: lightpink;
}

.skyblue:hover{
    background: skyblue;
}

.bg-card-img {
    width: 100px;
}

.bg-card-para {
    font-size: 19px;
}

@media(max-width: 426px){
    .bg-card-container {
        flex-direction: column;
        align-items: center;
    }

    .bg-card {
        padding: 20px;
    }

    .bg-card-para {
